import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import {NavDropdown} from 'react-bootstrap'

const useStyles = makeStyles((theme) => ({
  root: {
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    textAlign: '-webkit-center',
    marginTop: 20,
  },
  gridList: {
    width: 600,
  },
}));

export default function ImageGridList(props) {
  const classes = useStyles();
  const [imageData, setImageData] = React.useState(props.data);

  return (
    <div className={classes.root}>
      { imageData !== null && imageData !== undefined && imageData.length === 1 ? 
        <GridList cellHeight={260} className={classes.gridList} cols={1}>
        {imageData.map((tile) => (
          <GridListTile key={tile} cols={1}>
            <img src={tile} alt={tile}/>
          </GridListTile>
        ))}
      </GridList>
      : 
        imageData !== null && imageData !== undefined && imageData.length === 2 ? 
        <GridList cellHeight={260} className={classes.gridList} cols={1}>
        {imageData.map((tile) => (
          <GridListTile key={tile} cols={1}>
            <img src={tile} alt={tile}/>
          </GridListTile>
        ))}
        </GridList>
      :
        imageData !== null && imageData !== undefined && imageData.length === 3 ? 
        <GridList cellHeight={260} className={classes.gridList} cols={1}>
        {imageData.map((tile) => (
          <GridListTile key={tile} cols={2}>
            <img src={tile} alt={tile}/>
          </GridListTile>
        ))}
        </GridList>
      : 
        imageData !== null && imageData !== undefined && imageData.length >= 4 ? 
        <GridList cellHeight={260} className={classes.gridList} cols={4}>
        {imageData.map((tile) => (
          <GridListTile key={tile} cols={2}>
            <img src={tile} alt={tile}/>
          </GridListTile>
        ))}
        </GridList>
      : 
        <></>
      }
      <NavDropdown.Divider />
    </div>
  );
}